.app-wrapper {
    display: grid;
    grid-template-areas:
            'header header'
            'nav content';
    grid-template-rows:60px 1fr;
    grid-auto-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper__content {
    grid-area: content;
    background-color: #f8f8f8;
}






